.Navbar{
    display: flex;
    justify-content: space-evenly;
    padding: 16px;
    box-shadow: 0 1px 3px -2px black;
}

.ptag{
    color: whitesmoke;
    font-size: 38px;
    font-weight: 600px;
    /* margin-top: 10px; */
    margin-left: 200px;
    
}

.button button{
    margin-top:auto;
    width: 157px;
    height: 58px;
    outline: none;
    border: 1px solid #7a7a7a;
    border-radius: 75px;
    color: #515151;
    font-size: 20px;
    font-weight: 500;
    background: white;
    cursor: pointer;
}

.button button:active{
    background: #f5f5f5;
}

.main{
    gap: 100px;
    display: flex;
    align-items: center;
}
.header__searchInput {
    height: 12px;
    padding: 10px;
    
}
.xyz{
    background-color: #131921;
    display: flex;
    align-items: center;
    position: sticky;
    top: 0;
    z-index: 100;
    height: 100px;
}